@import url(https://fonts.googleapis.com/css?family=Roboto:400,400i,700);
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

* {
    font-family: Roboto, sans-serif;
    padding: 0;
    margin: 0;
}

html,
body {
    width: 100%;
    height: 100%;
}

.flexbox {
    background: linear-gradient(155deg, #55c7ed, #2bb3e0, #3bc1ed);
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.search {
    margin: 20px;
}

.search>h3 {
    font-weight: normal;
}

.search>h1,
.search>h3 {
    color: white;
    margin-bottom: 15px;
    text-shadow: 0 1px #0091c2;
}

.search>div {
    display: inline-block;
    position: relative;
    -webkit-filter: drop-shadow(0 1px #0091c2);
            filter: drop-shadow(0 1px #0091c2);
}

.search>div:after {
    content: "";
    background: white;
    width: 4px;
    height: 20px;
    position: absolute;
    top: 40px;
    right: 2px;
    -webkit-transform: rotate(135deg);
            transform: rotate(135deg);
}

.search>div>input {
    color: white;
    font-size: 16px;
    background: transparent;
    width: 25px;
    height: 25px;
    padding: 10px;
    border: solid 3px white;
    outline: none;
    border-radius: 35px;
    transition: width 0.5s;
}

.search>div>input::-webkit-input-placeholder {
    color: #efefef;
    opacity: 0;
    -webkit-transition: opacity 150ms ease-out;
    transition: opacity 150ms ease-out;
}

.search>div>input:-ms-input-placeholder {
    color: #efefef;
    opacity: 0;
    -ms-transition: opacity 150ms ease-out;
    transition: opacity 150ms ease-out;
}

.search>div>input::placeholder {
    color: #efefef;
    opacity: 0;
    transition: opacity 150ms ease-out;
}

.search>div>input:focus::-webkit-input-placeholder {
    opacity: 1;
}

.search>div>input:focus:-ms-input-placeholder {
    opacity: 1;
}

.search>div>input:focus::placeholder {
    opacity: 1;
}

.search>div>input:not(:-ms-input-placeholder) {
    width: 250px;
}

.search>div>input:focus,
.search>div>input:not(:placeholder-shown) {
    width: 250px;
}
ol {
    list-style: none;
    counter-reset: my-awesome-counter;
}

li {
    counter-increment: my-awesome-counter;
    margin: 0.5rem;
    margin-left: 1rem;
}

li::before {
    content: counter(my-awesome-counter);
    background: #662974;
    width: 2rem;
    height: 2rem;
    border-radius: 50%;
    display: inline-block;
    line-height: 2rem;
    color: white;
    text-align: center;
    margin-right: 0.5rem;
}

ol ol li::before {
    background: #DE51FF;
}

.result {
    margin-top: 2%;
    margin-left: 20%;
}
